	module.exports = {"data":{"markdownRemark":{"html":"<p>The JavaScript array method <code>.slice()</code> will return a new array object which will be a segment (a slice) of the original array. The original array is not modified.</p>\n<p><strong>Syntax</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>  array<span class=\"token punctuation\">.</span><span class=\"token function\">slice</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n  arr<span class=\"token punctuation\">.</span><span class=\"token function\">slice</span><span class=\"token punctuation\">(</span>startIndex<span class=\"token punctuation\">)</span>\n  arr<span class=\"token punctuation\">.</span><span class=\"token function\">slice</span><span class=\"token punctuation\">(</span>startIndex<span class=\"token punctuation\">,</span> endIndex<span class=\"token punctuation\">)</span> \n</code></pre>\n      </div>\n<h2>Parameters</h2>\n<ul>\n<li>\n<p><strong>startIndex</strong> The zero-based index where the slice should begin. If the value is omitted, it will start at 0.</p>\n</li>\n<li>\n<p><strong>endIndex</strong> The slice will end <strong>before</strong> this zero-based index. A negative index is used to offset from the end of the array. If the value is omitted, the segment will slice to the end of the array.</p>\n</li>\n</ul>\n<h2>Examples</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>  <span class=\"token keyword\">var</span> array <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">'books'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'games'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'cup'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'sandwich'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'bag'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'phone'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'cactus'</span><span class=\"token punctuation\">]</span>\n  \n  <span class=\"token keyword\">var</span> everything <span class=\"token operator\">=</span> array<span class=\"token punctuation\">.</span><span class=\"token function\">slice</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n  <span class=\"token comment\" spellcheck=\"true\">// everything = ['books', 'games', 'cup', 'sandwich', 'bag', 'phone', 'cactus']</span>\n  \n  <span class=\"token keyword\">var</span> kitchen <span class=\"token operator\">=</span> array<span class=\"token punctuation\">.</span><span class=\"token function\">slice</span><span class=\"token punctuation\">(</span><span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">)</span>\n  <span class=\"token comment\" spellcheck=\"true\">// kitchen = ['cup', 'sandwich']</span>\n  \n  <span class=\"token keyword\">var</span> random <span class=\"token operator\">=</span> array<span class=\"token punctuation\">.</span><span class=\"token function\">slice</span><span class=\"token punctuation\">(</span><span class=\"token number\">4</span><span class=\"token punctuation\">)</span>\n  <span class=\"token comment\" spellcheck=\"true\">// random = ['bag', 'phone', 'cactus']</span>\n  \n  <span class=\"token keyword\">var</span> noPlants <span class=\"token operator\">=</span> array<span class=\"token punctuation\">.</span><span class=\"token function\">slice</span><span class=\"token punctuation\">(</span><span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token operator\">-</span><span class=\"token number\">1</span><span class=\"token punctuation\">)</span>\n  <span class=\"token comment\" spellcheck=\"true\">// noPlats = ['books', 'games', 'cup', 'sandwich', 'bag', 'phone']</span>\n  \n  <span class=\"token comment\" spellcheck=\"true\">// array will still equal ['books', 'games', 'cup', 'sandwich', 'bag', 'phone', 'cactus']</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p>Source : <a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/slice' target='_blank' rel='nofollow'>MDN</a></p>\n<!-- Please add any articles you think might be helpful to read before writing the article -->","fields":{"slug":"/javascript/standard-objects/array/array-prototype-slice/"},"frontmatter":{"title":"Array.prototype.slice"}}},"pathContext":{"slug":"/javascript/standard-objects/array/array-prototype-slice/"}}

